  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  transform-origin: 0 100%;
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.link:hover:after {
  background-color: #00acdc;
}

.container_logo {
